@include font(SegoeUI-Light, SegoeUI-Light);
@include font(SegoeUI, SegoeUI);
@include font(SegoeUI-Italic, SegoeUI-Italic);
@include font(SegoeUI-Bold, SegoeUI-Bold);

@mixin segoel {
  font-family: "SegoeUI-Light", sans-serif; }

@mixin segoe {
  font-family: "SegoeUI", sans-serif; }

@mixin segoei {
  font-family: "SegoeUI-Italic", sans-serif; }

@mixin segoeb {
  font-family: "SegoeUI-Bold", sans-serif; }

body {
  background: #FFF;
  line-height: 1.4;
  @include segoe; }

.img-responsive {
  max-width: 100%; }

.h1 {
  color: #fff;
  @include segoeb;
  font-size: 48px; }

.h2 {
  color: #fff;
  @include segoeb;
  font-size: 40px;
  &--gold {
    text-align: center;
    color: #ffd700;
    text-transform: uppercase; }
  &--red {
    text-align: center;
    color: #e62f0f;
    text-transform: uppercase; }
  &--title {
    display: inline-block;
    padding-right: 15px;
    padding-left: 15px;
    background-color: #000;
    color: #ffd700;
    text-align: center;
    text-transform: uppercase; } }

.text-center {
  text-align: center; }

.mh-auto {
  margin-right: auto;
  margin-left: auto; }

.mt-20 {
  margin-top: 20px; }

.mb-20 {
  margin-bottom: 20px; }

//ICONS
.icon {
  &-leaflet {
    @include s(leaflet); }
  &-perfume {
    @include s(perfume); }
  &-shield {
    @include s(shield); }
  &-money-box {
    @include s(money-box); }
  &-fruit {
    @include s(fruit); }
  &-ok {
    @include s(ok); }
  &-document {
    @include s(document);
    @extend .mh-auto; }
  &-operator {
    @include s(operator);
    @extend .mh-auto; }
  &-truck {
    @include s(truck);
    @extend .mh-auto; }
  &-face-1 {
    @include s(face-1);
    @extend .mh-auto; }
  &-face-2 {
    @include s(face-2);
    @extend .mh-auto; }
  &-face-3 {
    @include s(face-3);
    @extend .mh-auto; } }
