$icons: map-merge($icons,('face-2': (X: -288px, Y:0px, W: 278px, H: 278px, TW: 926px, TH: 737px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('face-3': (X: 0px, Y:-288px, W: 278px, H: 278px, TW: 926px, TH: 737px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('fruit': (X: 0px, Y:-576px, W: 163px, H: 161px, TW: 926px, TH: 737px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('leaflet': (X: -787px, Y:-334px, W: 109px, H: 151px, TW: 926px, TH: 737px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('money-box': (X: -787px, Y:0px, W: 139px, H: 163px, TW: 926px, TH: 737px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('ok': (X: -331px, Y:-576px, W: 152px, H: 146px, TW: 926px, TH: 737px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('operator': (X: -576px, Y:0px, W: 201px, H: 201px, TW: 926px, TH: 737px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('perfume': (X: -787px, Y:-173px, W: 133px, H: 151px, TW: 926px, TH: 737px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('shield': (X: -173px, Y:-576px, W: 148px, H: 152px, TW: 926px, TH: 737px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('truck': (X: -288px, Y:-288px, W: 201px, H: 201px, TW: 926px, TH: 737px, IMG: '../img/sprite.png')));


// Gets an attribute from the sass map
@function icon-attr($icon, $attr) {
  $icon: map-get($icons, $icon);
  @return map-get($icon, $attr); }

@mixin sprite($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  width: icon-attr($iconName, W);
  height: icon-attr($iconName, H);
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-position($iconName) {
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-retina($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  $width: icon-attr($iconName, W);
  $height: icon-attr($iconName, H);
  width: $width/2;
  height: $height/2;
  $x: icon-attr($iconName, X);
  $y: icon-attr($iconName, Y);
  background-position: $x/2 $y/2;
  $tw: icon-attr($iconName, TW);
  $th: icon-attr($iconName, TH);
  background-size: $tw/2 $th/2; }


@mixin s($i) {
  @include sprite($i); }
@mixin sp($i) {
  @include sprite-position($i); }
@mixin sr($i) {
  @include sprite-retina($i); }

